<template>
  <transition name="slide-up">
    <div class="what-we-do-box text-right" dir="rtl" v-show="!show">
      <div class="overlay">
        <h3><i :class="icon"></i> {{heading}}</h3>
        <p>
          <slot></slot>
        </p>
        <span><a href="#">{{span}}</a></span>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'what-we-do-box',
  data(){
    return{
      shoooow: true
    }
  },
  props: {
    heading: {default: 'Heading is here'},
    icon: {default: 'fas fa-heart'},
    span: {default: 'default value'},
    show: {default: true}
    // background: {default: '#000'}
  }
}
</script>

<style lang="scss" scoped>
.what-we-do-box{
  background-size: cover;
  height: 230px;
  position: relative;
  color: #fff;
  font-family: var(--third-font);
  cursor: pointer;
  overflow: hidden;
  .overlay{
    position: absolute;
    top: 150px;
    left: 0;
    background-color: rgba(0,46,91,.9);
    width: 100%;
    height: 100%;
    padding: 15px;
    transition: all 0.4s ease;
    h3{
      margin:0;
      font-size: 22px;
      font-weight: bold;
      svg{
      color: var(--second-color);
      margin-left: 15px;
      font-size: 40px;
      line-height: 9;
      position: relative;
      top: 5px;
      }
    }
    P{
      margin: 30px 0 20px 0;
      font-size: 15px;
      line-height: 1.8
    }
    span a{
      color: var(--second-color);
      font-size: 14px;
      text-decoration: none
    }
  }
}

.what-we-do-box:hover .overlay{
  top: 0
}

/*==== Transition ====*/
.slide-up-enter-active,
.slide-up-leave-active{
  transition: all 1s ease-in-out;
}
.slide-up-leave{
  top: 320px;
  opacity: 0;
}
.slide-up-leave-to{
  top: 0;
  opacity: 1;
}
.slide-up-enter{
  top: 320px;
  opacity: 0;
}
.slide-up-enter-to{
  top: 0;
  opacity: 1;
}
/*==== Transition ====*/

@media (max-width: 992px){
  .what-we-do-box{
    margin-bottom: 30px;
    .overlay{
      top: 0 !important;
    }
  }
  .what-we-do .box-holder:nth-child(3) .what-we-do-box{
    margin-bottom: 0
  }
}
</style>