<template>
  <div class="post-page">
    <button @click="postDisplay()">ahmnedj</button>
    <news-post :title="item.name" :body="item.body" :author="item.email"></news-post>
    <h1>post {{id}}</h1>
  </div>
</template>

<script>
import newsPost from "./news-post.vue";

  export default {
    name: "post-page",
    components: {
      newsPost
    },
    data() {
      return {
        id: this.$route.params.id,
        item: {},
        postNumber: 1
      }
    },
    methods: {
      async postDisplay() {
        const res = await fetch(`https://jsonplaceholder.typicode.com/comments/${this.id}`);
        const items = await res.json();
        this.item = items;
        console.log(this.id)
      }
    }
  }
</script>

<style lang="scss" scoped>
.post-page{
  padding-top: 100px
}
</style>