<template>
  <div class="news-box">
    <div class="news-img">
      <div class="overlay"><span class="fas fa-plus"></span></div>
      <slot></slot>
    </div>
    <h3><a href="#">{{title}}</a></h3>
    <span>{{date}}</span>
    <i class="fas fa-clock"></i>
  </div>
</template>

<style lang="scss" scoped>
.news-box{
  text-align: right;
  font-family: var(--third-font);
  .news-img{
    position: relative;
    .overlay{
      background-color: rgba(0, 0, 0, 0.4);
      cursor: pointer;
      opacity: 0;
      transition: opacity 0.5s ease-in-out;
      svg{
        color: #fff;
        font-size: 40px;
        display: flex;
        position: relative;
        top: calc(50% - 20px);
        margin: 0 auto;
      }
    }
  }
  .news-img img{
    width: 100%;
    cursor: pointer;
  }
  .news-img:hover .overlay{
    opacity: 1
  }
  h3{
    position: relative;
    margin-top: 40px;
    margin-bottom: 15px
  }
  h3 a{
    font-size: 21px;
    color: var(--main-color);
    text-decoration: none;
    font-weight: bold
  }
  h3::before{
    content: '';
    position: absolute;
    top: -12.5px;
    right: 0;
    width: 50px;
    height: 4px;
    background-color: var(--second-color);
    border-radius: 4px;
  }
  h3:hover a{
    color: #6c98e1
  }
  svg{
    color: var(--second-color);
    margin-left: 10px;
    font-size: 18px;
    position: relative;
    top: 3px;
  }
  span{
    color: #777
  }
}

@media (max-width: 992px){
  .news-box{
    margin-bottom: 40px
  }
}
</style>

<script>
export default {
  name: 'news-box',
  props: {
    title: {default: 'title is here'},
    date: {default: 'June 14, 2020'}
  }
}
</script>