<template>
  <div class="gallery-box">
    <div class="overlay">
      <div class="gallery-info">
        <h4 :class="middle">{{title}}</h4>
        <span>{{cat}}</span>
      </div>
    </div>
    <slot></slot>
  </div>
</template>

<script>
export default {
  name: 'gallery-box',
  props: {
    title: {default: "Here Is Title Or quistion"},
    cat: {default: "Category Word"},
    middle: {}
  }
}
</script>

<style lang="scss" scoped>
.gallery-box{
  position: relative;
  margin-bottom: 30px;
  cursor: pointer;
  overflow: hidden;
  .gallery-info{
    color: #fff;
    padding: 0 20px;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-flow: column;
    h4{
      font-size: 19px;
      font-weight: bold;
      line-height: 1.8;
      position: relative;
      top: -80px;
      transition: all 0.4s ease-in-out;
    }
    h4::before{
      content: '';
      position: absolute;
      top: -15px;
      right: 0;
      width: 40px;
      height: 4px;
      background-color: var(--second-color);
    }
    h4.before-middle::before{
      right: calc(50% - 20px)
    }
    span{
      font-size: 18px;
      color: #fff;
      position: relative;
      bottom: -80px;
      transition: all 0.4s ease-in-out;
    }
  }
  img{
    width: 100%
  }
  .overlay{
      opacity: 0;
      transition: opacity 0.5s ease-in-out;
      background-color: rgba(0, 46, 91, 0.7) !important;
    }
}
.gallery-box:hover{
