<template>
  <h5 @click="toto()">{{fofo}}</h5>
</template>

<script>
export default {
  name: 'counter',
  props: {
    number: {default: 100}
  },
  data: function () {
    return {
      counter: 0,
      end: this.number
    }
  },
  methods: {
    toto: function () {
      setInterval(() => {
        if (this.counter < this.end) {
          this.counter = this.counter + 1
        }
      }, 4);
    }
  },
  computed: {
    fofo: function () {
      return this.counter
    }
  }
}
</script>

<style lang="scss" scoped>
.counter{
  display: block;
}
</style>