<template>
  <div class="observer-div">

  </div>
</template>

<script>
export default {
  name: 'observer',
  data: function (){
    return {
      observer: null
    }
  },
  mounted() {
    this.observer = new IntersectionObserver((th) => {
      if (!th[0].isIntersecting) {
        return;
      } else{
        this.$emit('visible')
      }
    }, {threshold: 0.8});
    this.observer.observe(this.$el)
  }
}
</script>

<style lang="scss" scoped>
.observer-div{
  height: 1px
}
</style>