      observer
    },
    data() {
      return{
        pageNumer: 1,
        items: [],
        tolink: ""
      }
    },
    methods: {
      async visibled() {
        const res = await fetch(`https://jsonplaceholder.typicode.com/comments?_page=${this.pageNumer++}`);
        const items = await res.json();
        this.items = [...this.items, ...items]
        this.counter++
      }
    }
  }
</script>

